.welcome-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    height: 87.5vh;
    box-sizing: border-box;
    overflow: auto;
    flex: auto;
}

.loader1 {
    width: 60px;
    aspect-ratio: 2;
    --_g: no-repeat radial-gradient(circle closest-side,#fff 90%,#0000);
    opacity: 50%;
    background: 
      var(--_g) 0%   50%,
      var(--_g) 50%  50%,
      var(--_g) 100% 50%;
    background-size: calc(100%/3) 50%;
    animation: l3 1s infinite linear;
  }
  @keyframes l3 {
      20%{background-position:0%   0%, 50%  50%,100%  50%}
      40%{background-position:0% 100%, 50%   0%,100%  50%}
      60%{background-position:0%  50%, 50% 100%,100%   0%}
      80%{background-position:0%  50%, 50%  50%,100% 100%}
  }

.logo_welcome {
    width: 40vw;
    height: 40vw;
    animation: drop-from-top 0.45s forwards;
    margin-bottom: 3vh;
}

.hello-text {
    margin-top: 2vh;
    text-align: center;
    font-weight: bold;
    color: white;
    font-size: 8vw;
}

.listing-text {
    text-align: center;
    font-weight: bold;
    color: white;
    opacity: 50%;
    font-size: 5vw;
}